body {
  background-color: orange;
  color:white;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif
}
p {
  line-height: 1.5;
}

h1 {
  text-align: center;
}

.container {
  width: 800px;
  padding: 10px;
  margin: auto;
}

.container section {
  margin: 10px 0;
  position: relative;
  padding: 10px;
  background: darkmagenta;
  border-radius: 10px;
  height: 240px;
  display: -ms-flexbox;
  display: flex;
}

#slider > .image-container {
  -ms-flex-positive: 10;
      flex-grow: 10;
  height:230px;
  text-align: center;
}

img {
  border-radius: 10px;
  height: 230px;
  border: 100px;
}

.roster-image-container {
  -ms-flex-positive: 1;
      flex-grow: 1;
  text-align: center;
  padding: 1px;
}

.button {
  -ms-flex-positive: 1;
      flex-grow: 1;

}

.button:hover {
  cursor: pointer;
}

#bio .image {
  -ms-flex-positive: 1;
      flex-grow: 1;
}

#bio .info {
  -ms-flex-positive: 2;
      flex-grow: 2;
  padding: 10px 30px;
}
#bio .info p:last-child {
  text-indent: 20px;
}





